@font-face {
  font-family: "Gotham Pro";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/GothamProRegular.woff2") format("woff2"), url("../fonts/GothamProRegular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/GothamProBold.woff2") format("woff2"), url("../fonts/GothamProBold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  margin: 0 20px;
}
.wrapper--header {
  margin: 2px 10px;
}
.wrapper--advantages, .wrapper--products {
  margin: 0;
}
@media (min-width: 992px) {
  .wrapper {
    margin: 0 36.5px;
  }
  .wrapper--advantages, .wrapper--products {
    margin: 0;
  }
}

.button {
  display: block;
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  color: #ffffff;
  border: none;
  border-radius: 30px;
  background-color: #dd3037;
  padding: 13px 36px;
  margin: 0 auto;
  cursor: pointer;
}
.button--modal {
  position: relative;
  left: 6px;
  background-color: transparent;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  padding: 0;
}
.button--modal::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 36px;
  height: 36px;
  background: url("../image/icon-close.svg") no-repeat;
  background-size: cover;
}
.button--close::before {
  width: 23px;
  height: 22px;
  background: url("../image/icon-close.svg") no-repeat;
}
@media (min-width: 992px) {
  .button--modal {
    width: 40px;
    height: 40px;
  }
  .button--modal::before {
    width: 24px;
    height: 24px;
    background-size: cover;
  }
  .button--close::before {
    width: 33px;
    height: 33px;
    background-size: cover;
  }
}

.button-more {
  position: relative;
  display: block;
  background-color: #21a038;
  width: 25px;
  height: 25px;
  border: none;
  border-radius: 50%;
  padding: 0;
  margin: 0 auto;
  cursor: pointer;
}
.button-more::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 23px;
  height: 22px;
  background: url("../image/icon-close.svg") no-repeat;
}
.button-more--close::before {
  width: 15px;
  height: 15px;
  background: url("../image/icon-info-more.svg") no-repeat;
}
@media (min-width: 992px) {
  .button-more {
    width: 40px;
    height: 40px;
  }
  .button-more::before {
    width: 33px;
    height: 33px;
    background-size: cover;
  }
  .button-more--close::before {
    width: 24px;
    height: 24px;
    background-size: cover;
  }
}

.list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.link {
  text-decoration: none;
  color: #ffffff;
  margin: 0;
  cursor: pointer;
}

.title {
  font-size: 18px;
  font-weight: 700;
  letter-spacing: -0.6px;
  text-transform: uppercase;
  color: #999999;
  text-align: center;
  margin: 0;
}
@media (min-width: 992px) {
  .title {
    font-size: 28px;
  }
}

.br {
  display: none;
}
@media (min-width: 360px) {
  .br {
    display: block;
  }
}
@media (min-width: 388px) {
  .br {
    display: none;
  }
}

.tabs-list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
.tabs-list--products {
  gap: 4px;
}
.tabs-list--footer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
}
@media (min-width: 992px) {
  .tabs-list {
    justify-content: center;
    gap: 20px;
  }
}

.tabs-list__item {
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  padding: 0 11px;
  line-height: 24px;
  border: 3px solid #999999;
  border-radius: 5px;
  text-align: center;
}
.tabs-list__item--current {
  background-color: #999999;
  color: #212121;
}
.tabs-list__item--products {
  padding: 0 32px;
}
@media (max-width: 359px) {
  .tabs-list__item {
    font-size: 10px;
  }
}
@media (min-width: 992px) {
  .tabs-list__item {
    font-size: 20px;
    font-weight: 500;
    padding: 12.5px 30px;
  }
}

.page {
  height: 100%;
  font-family: "Gotham Pro", "Arial", "Helvetica", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: normal;
}

.page__body {
  margin: 0;
}

.body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  background-color: #212121;
  color: #ffffff;
}

.header {
  position: relative;
  z-index: 2;
  background-color: #111311;
}
@media (min-width: 992px) {
  .header {
    background-color: #323232;
  }
}

@media (min-width: 992px) {
  .header__nav-bar {
    margin: 0 auto;
    order: 2;
  }
}

.header__wrapper {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
@media (min-width: 992px) {
  .header__wrapper {
    min-height: 70px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
  }
}

.header__logo {
  margin-right: auto;
  margin-left: auto;
  position: relative;
  left: 24px;
  cursor: pointer;
}
@media (min-width: 992px) {
  .header__logo {
    order: 1;
    margin: 0;
  }
}

@media (min-width: 992px) {
  .header__phones-list {
    order: 3;
  }
}

.nav-bar__wrapper {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 270px;
  display: flex;
  flex-direction: column;
  gap: 40px 10px;
  height: 100vh;
  background-color: #111311;
}
@media (min-width: 992px) {
  .nav-bar__wrapper {
    position: relative;
    z-index: 0;
    display: block;
    width: auto;
    height: auto;
    background-color: transparent;
  }
}

.nav-bar__site-list {
  margin: 40px 0 0;
}
@media (min-width: 992px) {
  .nav-bar__site-list {
    margin-top: 40px;
  }
}

.nav-bar__toggle {
  width: 36px;
  height: 36px;
  border: none;
  background-color: transparent;
  padding: 0;
}
.nav-bar__toggle::before {
  content: "";
  position: absolute;
  top: 13px;
  left: 20px;
  width: 20px;
  height: 3px;
  border-radius: 2px;
  background-color: #ffffff;
  box-shadow: 0 5px 0 0 #ffffff, 0 10px 0 0 #ffffff;
}
@media (min-width: 992px) {
  .nav-bar__toggle {
    display: none;
  }
}

.nav-bar__wrapper-button {
  background-color: #21a038;
}
@media (min-width: 992px) {
  .nav-bar__wrapper-button {
    display: none;
  }
}

.nav-bar--opened .nav-bar__wrapper {
  display: flex;
}

.nav-bar--opened .nav-bar__toggle::before, .nav-bar--opened .nav-bar__toggle::after {
  content: "";
  position: absolute;
  top: 18px;
  left: 16px;
  width: 24px;
  height: 3px;
  border-radius: 2px;
  background-color: #ffffff;
}
.nav-bar--opened .nav-bar__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.nav-bar--opened .nav-bar__toggle::after {
  transform: rotate(-45deg);
}

.nav-bar--nojs .nav-bar__toggle {
  display: none;
}

.nav-bar--closed .nav-bar__wrapper {
  display: none;
}
@media (min-width: 992px) {
  .nav-bar--closed .nav-bar__wrapper {
    display: flex;
  }
}

.nav-bar--nojs .nav-bar__wrapper {
  display: flex;
}

.site-list {
  width: 100%;
}
@media (min-width: 992px) {
  .site-list {
    display: flex;
    flex-wrap: wrap;
    gap: 10px 33px;
    max-width: 234px;
    margin: 0 auto;
  }
}

.site-list__item {
  position: relative;
  display: flex;
  align-items: center;
  gap: 6px;
  min-height: 60px;
  border-left: none;
  border-top: 0.5px solid #21a038;
  border-bottom: 0.5px solid #21a038;
  border-right: 0.5px solid #21a038;
  box-shadow: inset 0 0 5px #21a038;
}
@media (min-width: 992px) {
  .site-list__item {
    border: none;
    box-shadow: none;
  }
}

.site-list__icon {
  margin-left: 20px;
  min-width: 25px;
}
@media (min-width: 992px) {
  .site-list__icon {
    display: none;
  }
}

.site-list__link {
  display: block;
  font-size: 13px;
  font-weight: 700;
  line-height: 1.3;
  padding: 16px 5px;
  text-transform: uppercase;
}
@media (min-width: 992px) {
  .site-list__link {
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 700;
  }
}

@media (min-width: 992px) {
  .phones-list {
    justify-self: right;
  }
}

@media (min-width: 992px) {
  .phones-list__item {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 3px;
  }
}

.phones-list__city {
  display: none;
}
@media (min-width: 992px) {
  .phones-list__city {
    display: block;
    font-size: 12px;
    color: #979799;
    margin-left: auto;
  }
}

.phones-list__address {
  font-style: normal;
}

.phones-list__link {
  font-size: 9px;
  font-weight: 700;
}
@media (min-width: 992px) {
  .phones-list__link {
    font-size: 12px;
  }
}

.main {
  flex-grow: 1;
}

.promo {
  position: relative;
  text-align: center;
  padding: 272px 0 45px 0;
  background: url("../image/header-background.jpg") no-repeat;
  background-position: 75% 47%;
  background-size: cover;
  filter: drop-shadow(0 5px 3px rgba(255, 255, 255, 0.2));
}
@media (min-width: 992px) {
  .promo {
    padding: 340px 0 105px;
    background-size: cover;
    background-position: 77% 62%;
