  }
}

.promo__title {
  font-size: 29.5px;
  color: #ffffff;
  margin-bottom: 22px;
}
@media (min-width: 992px) {
  .promo__title {
    font-size: 64px;
  }
}

.promo__slogan {
  font-size: 17.5px;
  font-weight: 700;
  margin-bottom: 35px;
}
@media (min-width: 992px) {
  .promo__slogan {
    font-size: 28px;
    margin-bottom: 45px;
  }
}

.about-us {
  padding: 45px 0;
  text-align: center;
}
@media (min-width: 992px) {
  .about-us {
    padding: 77px 0 28px;
  }
}

.about-us__container {
  display: flex;
  flex-direction: column;
  row-gap: 35px;
}
@media (min-width: 992px) {
  .about-us__container {
    row-gap: 80px;
  }
}
@media (min-width: 1440px) {
  .about-us__container {
    max-width: 1300px;
    margin: 0 auto;
  }
}

.about-us-list {
  display: flex;
  flex-direction: column;
  row-gap: 34px;
  justify-content: center;
}
@media (min-width: 992px) {
  .about-us-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 10px;
  }
}

@media (min-width: 992px) {
  .about-us-list__item {
    max-width: 330px;
  }
}

.about-us-list__container {
  display: flex;
  width: 80px;
  height: 69px;
  margin: 0 auto 14px;
  border: 2px solid #b6ffba;
  border-radius: 5px;
  background-color: rgba(0, 0, 0, 0.7);
