}
.about-us-list__text--city {
  max-width: 72%;
}
@media (min-width: 992px) {
  .about-us-list__text--city {
    max-width: 79%;
  }
}

.department {
  padding: 45px 0;
}
@media (min-width: 992px) {
  .department {
    padding: 70px 0;
  }
}

.department__container {
  display: flex;
  flex-direction: column;
  row-gap: 35px;
}
@media (min-width: 992px) {
  .department__container {
    max-width: 1300px;
    margin: 0 auto;
    row-gap: 45px;
  }
}

@media (min-width: 1440px) {
  .department__list {
    max-width: 1300px;
    margin: 0 auto;
  }
}

@media (min-width: 992px) {
  .department__vacancy-list {
    width: 775px;
    margin: 0 auto;
  }
}

.department__title {
  text-align: left;
  letter-spacing: -0.8px;
}
@media (min-width: 992px) {
  .department__title {
    margin: 0 auto;
  }
}

.vacancy-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.vacancy-list__item {
  display: grid;
  grid-template-columns: 1fr 25px;
  align-items: start;
  padding: 27px;
  border: 3px solid #999999;
  border-radius: 30px;
}
@media (min-width: 992px) {
  .vacancy-list__item {
    grid-template-columns: 1fr 45px;
  }
}

.vacancy-list__box {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
@media (min-width: 992px) {
  .vacancy-list__box {
    gap: 25px;
  }
}

.vacancy-list__container {
  display: grid;
  grid-template-columns: 25px 1fr;
  align-items: center;
  gap: 10px;
}
@media (min-width: 992px) {
  .vacancy-list__container {
    grid-template-columns: 45px 1fr;
  }
}

.vacancy-list__icon-box {
  position: relative;
  display: block;
  width: 25px;
  height: 25px;
  background-color: #ffffff;
  border-radius: 50%;
}
.vacancy-list__icon-box--vacancy::before, .vacancy-list__icon-box--price::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 15px;
  height: 15px;
}
.vacancy-list__icon-box--vacancy::before {
  background: url("../image/icon-info-vacancy.svg") no-repeat;
}
.vacancy-list__icon-box--price::before {
  background: url("../image/icon-info-price.svg") no-repeat;
}
@media (min-width: 992px) {
  .vacancy-list__icon-box {
    width: 40px;
    height: 40px;
  }
  .vacancy-list__icon-box--vacancy::before, .vacancy-list__icon-box--price::before {
    width: 24px;
    height: 24px;
    background-size: cover;
  }
}

.vacancy-list__name {
  font-size: 18px;
  font-weight: 700;
  margin: 0;
}
@media (min-width: 992px) {
  .vacancy-list__name {
    font-size: 28px;
  }
}

.vacancy-list__salary {
  position: relative;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: -0.8px;
  text-transform: uppercase;
  color: #21a038;
  margin: 0;
}
.vacancy-list__salary--discussion {
  text-transform: none;
  letter-spacing: normal;
}
@media (min-width: 992px) {
  .vacancy-list__salary {
    font-size: 28px;
  }
}

.vacancy-list__button {
  position: relative;
  top: 22px;
}

.vacancy-list__item:nth-of-type(2) .vacancy-list__button,
.vacancy-list__item:nth-of-type(3) .vacancy-list__button {
  top: 31px;
}

.conditions-list {
  display: none;
  font-size: 12px;
  font-weight: 700;
  width: 86%;
}
.conditions-list--open {
  display: block;
}
@media (min-width: 992px) {
  .conditions-list {
    font-size: 18px;
    width: 100%;
  }
}

.conditions-list__name {
  margin: 10px 0;
}

.conditions-list__description {
  position: relative;
  margin: 0;
  padding-left: 20px;
}
.conditions-list__description::before {
  content: "";
  position: absolute;
  top: 6px;
  left: 10px;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: #ffffff;
}

.advantages {
  padding: 46px 0;
  background-color: #111311;
}

@media (min-width: 992px) {
  .advantages__container {
    max-width: 770px;
    margin: 0 auto;
  }
}

.advantages__title {
  margin: 0 10px 20px;
}

.advantages-list__item {
  position: relative;
  display: grid;
  grid-template-columns: 20px 1fr 25px 20px;
  padding: 16.5px 0 14.8px;
}
.advantages-list__item::after {
  content: "";
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 3px;
  background-color: #999999;
}

.advantages-list__container {
  grid-column: 2/3;
}

.advantages-list__button {
  grid-column: 3/4;
}

.advantages-list__title {
  font-size: 17.5px;
  font-weight: 700;
  margin: 0;
}

.advantages-list__description {
  display: none;
  font-size: 18px;
  font-weight: inherit;
  margin: 16.5px 0 0;
}
.advantages-list__description--open {
