.products {
  padding: 47px 0 30px;
}

.products__container {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 35px;
  width: 100%;
}

.products__title {
  padding: 0 20px;
}

.products__list {
  padding: 0 20px;
  margin: 0 auto;
}

.products__controls {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.products__swiper-pagination {
  margin: -19px auto 0;
}

.products-carousel__item {
  width: 165px;
  height: 182px;
  background-color: #d9d9d9;
  border-radius: 10px;
  border: 3px solid rgba(0, 0, 0, 0.7);
  margin: 0 auto;
}

.products-carousel__item:not(:nth-of-type(1)) {
