}

.controls__button-prev {
  background: linear-gradient(to left, #999999, #ffffff);
  margin-right: auto;
  left: -39px;
}
.controls__button-prev--career {
  background: transparent;
  left: -49px;
}

.controls__button-forward {
  background: linear-gradient(to left, #ffffff, #999999);
  margin-left: auto;
  right: -39px;
}
.controls__button-forward--career {
  background: transparent;
  right: -49px;
}

.swiper-pagination {
  display: flex;
  gap: 9px;
}

.swiper-pagination__bullet {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #888888;
}
.swiper-pagination__bullet--current {
  width: 42.6px;
  background-color: #ffffff;
  border-radius: 25px;
}

.career {
  padding: 45px 12px 48px;
  background-color: #111311;
}

.career__box {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 35px;
}
@media (min-width: 992px) {
  .career__box {
    max-width: 770px;
    margin: 0 auto;
  }
}

.career__text-container {
  display: flex;
  flex-direction: column;
  gap: 15px;
  padding: 0 8px;
}

.career__text {
  font-size: 13.5px;
  font-weight: 700;
  line-height: 20px;
  margin: 0;
}

.career__container {
  position: relative;
}

.career__controls {
  position: absolute;
  top: 48px;
  z-index: 2;
}

.career-carousel__item {
  position: relative;
  text-align: center;
}
.career-carousel__item::after {
  content: "";
  position: absolute;
  top: 228px;
  left: 0;
  right: 0;
  margin: auto;
  width: 240px;
  height: 13px;
  background: url("../image/line.svg") no-repeat;
}
.career-carousel__item::before {
  content: "";
  position: absolute;
  top: -153px;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  box-shadow: 0 -96px 111px 0 #7fce84;
}

.career-carousel__item:not(:nth-of-type(1)) {
  display: none;
}

.career-carousel__image {
  position: relative;
  z-index: 2;
  display: block;
  object-fit: contain;
  margin: 0 auto;
}

.career-carousel__name {
  font-size: 16px;
  font-weight: 700;
  text-align: center;
  margin: 16px 0 3px;
}

.career-carousel__position {
  text-align: center;
  margin: 0 auto 19px;
}

.career-description-list {
  display: flex;
  flex-direction: column;
  gap: 19px;
  text-align: left;
  list-style-type: disc;
  margin: 0 10px;
}

.career-description-list__item {
  font-size: 13.5px;
  margin-left: 19px;
  line-height: 19px;
}

.successes {
  padding: 45px 0 31px;
  background: url("../image/background-successes-mobile.svg") no-repeat, linear-gradient(120deg, #767e7700, #003e0b6e, #767e7700);
  background-position: 50% 48%;
  background-size: contain;
}

@media (min-width: 992px) {
  .successes__wrapper {
    max-width: 770px;
    margin: 0 auto;
  }
}

.successes__box {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-bottom: 137px;
}

.successes__text {
  font-size: 18px;
  font-weight: 700;
  letter-spacing: -0.6px;
  line-height: 24px;
  text-transform: uppercase;
  margin: 0;
}

.successes__container {
  padding: 20px 26.5px;
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 20px;
  border: 3px solid #36853b;
  filter: drop-shadow(0 0 5px #36853b);
}
@media (min-width: 992px) {
  .successes__container {
    max-width: 431px;
    margin: 0 auto;
  }
}

.successes__slogan {
  font-size: 18px;
  font-weight: 700;
  margin: 0 auto 23px;
  letter-spacing: -0.5px;
}

.successes__button {
  padding: 13px 34px;
}
@media (max-width: 359px) {
  .successes__button {
    padding: 13px;
  }
}

.footer {
  padding: 48px 0 0;
}

.footer__container {
  display: flex;
  flex-direction: column;
  gap: 38px;
}

.footer__title {
  letter-spacing: 1.2px;
}

.footer__list {
  margin: 0 auto;
}

.footer__social-list {
  margin: 9px auto 42px;
}

.footer__location {
  position: relative;
}

.footer__map {
  position: relative;
  width: 100%;
  z-index: 2;
  display: block;
  outline: none;
}

.footer__map-image {
  position: absolute;
  object-fit: cover;
  width: 100%;
  max-height: 223px;
  top: 0;
  left: 0;
  z-index: 1;
}

.contacts-list {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.contacts-list__item {
  display: flex;
  align-items: center;
  gap: 10px;
}

.contacts-list__address {
  font-style: normal;
}

.contacts-list__link,
.contacts-list__location {
  font-size: 12px;
  letter-spacing: 0.35px;
}

.contacts-list__icon-box {
  position: relative;
  top: 1px;
  left: 2px;
  display: block;
  min-width: 32px;
  height: 32px;
  border-radius: 50%;
  border: 1px solid #21a038;
}
.contacts-list__icon-box::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  background-size: contain;
}
.contacts-list__icon-box--phone::before {
  background: url("../image/icon-footer-phone.svg") no-repeat;
  width: 18px;
  height: 14px;
}
.contacts-list__icon-box--email::before {
  background: url("../image/icon-footer-email.svg") no-repeat;
  width: 18px;
  height: 14px;
  left: -2px;
}
.contacts-list__icon-box--location::before {
  background: url("../image/icon-footer-location.svg") no-repeat;
  width: 20px;
  height: 18px;
  left: -2px;
}

.contacts-list__location {
  margin: 0;
  line-height: 26px;
  letter-spacing: normal;
}

.social-list {
  display: flex;
  flex-wrap: wrap;
  gap: 38px;
}

.social-list__link--vk, .social-list__link--telegram {
  position: relative;
  top: -1px;
  left: -3px;
}

.copyright {
  padding: 44px 0 51px;
  text-align: center;
}

.copyright__container {
  display: flex;
  flex-direction: column;
  width: 259px;
  margin: 0 auto;
}

.copyright__text {
  font-weight: 700;
  line-height: 19px;
  margin: 0 auto 32px;
}

.copyright__requisites,
.copyright__politics {
  text-decoration: underline;
  line-height: 20px;
}

.copyright__politics {
  font-size: 12px;
  margin-bottom: 15px;
}

.modal-container {
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  min-height: 100vh;
  background: #262626;
  z-index: 4;
  visibility: hidden;
  overflow-y: auto;
}
.modal-container--open {
  visibility: visible;
}

.modal {
  position: relative;
  width: 100%;
}

.modal__wrapper {
  background-color: rgba(0, 0, 0, 0.7);
  padding: 4px 20px;
}

.modal__close-button {
  margin: 0;
  margin-left: auto;
}

.application-file {
  padding: 45px 0;
}

.application-file__title {
  margin-bottom: 39px;
}

.application-file__container {
  position: relative;
}
.application-file__container::after {
  content: "";
  position: absolute;
  top: -317px;
  left: 3px;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 90px;
  height: 90px;
  background: url("../image/icon-sent.svg") no-repeat;
}

.application-file__text {
  font-size: 18px;
  font-weight: 700;
  width: 75%;
  text-align: center;
  letter-spacing: -0.6px;
  margin: 221px auto 0;
}
.application-file__text--subtext {
  letter-spacing: normal;
  margin: 18px auto 101px;
}

.application-file__link {
  width: 184px;
  box-sizing: border-box;
  text-align: center;
  margin: 0 auto;
}

.application-form__fieldset {
  padding: 0;
  margin: 0 0 20px;
  border: none;
}
.application-form__fieldset--resume {
  margin-bottom: 36px;
}
.application-form__fieldset--approval {
  margin-bottom: 16px;
}

.application-form__input {
  display: block;
  width: 100%;
  box-sizing: border-box;
  margin: 0;
  padding: 14px 10px;
  border: none;
  border-bottom: 3px solid #999999;
  font-size: 19px;
  color: #999999;
  background-color: transparent;
}

.application-form__label {
  font-size: 18px;
  font-weight: 700;
  margin-left: 10px;
}

.application-form__select {
  background-image: url("../image/icon-select.svg");
  background-repeat: no-repeat;
  background-position: right 11px center;
  appearance: none;
}

.application-form__tip {
  font-size: 11.5px;
  width: 67%;
  margin: 12px 0 17px 10px;
}

.application-form__upload-label {
  background-color: #21a038;
  max-width: 184px;
  box-sizing: border-box;
  padding: 13px 33px;
  margin: 0;
}

.application-form__control {
  position: relative;
}

.application-form__control-mark {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 12px;
  height: 12px;
  border: 0.5px solid #d9d9d9;
  border-radius: 3px;
  box-sizing: border-box;
  background-color: #323232;
}

.application-form__control-icon {
  display: none;
  position: absolute;
  z-index: 1;
  top: 2px;
  left: 3px;
}

.application-form__control-label {
  font-size: 9.5px;
  line-height: 15px;
  margin: 0 0 20px 17px;
}

.application-form__control-text {
  display: block;
  margin-top: 13px;
}

.application-form__control-input:checked + .application-form__control-icon {
  display: block;
}

.application-form__submit {
  width: 184px;
  padding: 13px;
}